.referral-table{
  width: 100%;
  th,td{width:33%;}

}

.info-box {
    text-align: left;
    width: auto;
    max-width: 250px;
    margin: auto auto 20px auto;
    padding: 16px;
    border-radius: 3px;
}

.info-box.fail {
    border: 1px solid #e5003c;
    background-color: #ffc4be;
    color: #e5003c;
}

.info-box.success {
    border: 1px solid green;
    background-color: #e2ffd9;
    color: green;
}
